  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  /*background-color: #61dafb;*/
  height: 100vh;
  font-family: Arial;
}

h2{
  font-family: Roboto Condensed;
}


