.title {
  font-size: 40px;
  font-family: Montserrat;
  color: rgb(15, 15, 15);
  margin: auto;
  margin-left: 0%;
}

.iconAndTitleWrapper {
  display: flex;
  margin-top: 10vh;
}

.iconTitle {
  align-items: center;
  margin: auto;
  margin-right: 0%;
  height: 40px;
}

.slogan {
  font-size: 20px;
  font-family: Comfortaa;
  font-style: italic;
  color: rgb(54, 55, 58);
  margin-top: 5vh;
}

.ingredientListWrapper {
  display: flex;
  flex-direction: column;
  background-color: white;
  width: 70%;
  margin: auto;
  margin-top: 11vh;
  border-radius: 8px;
  padding: 5vw;
  padding-top: 2%;
  padding-bottom: 5%;
}

.ingredientListHeader {
  font-size: 3vh;
  margin-top: 0%;
}

.ingredientList a {
  text-decoration: none;
  color: rgb(54, 55, 58);
}

.ingredientList a:hover {
  color: rgb(138, 138, 139);
}

.ingredientList {
  margin-top: 2vh;
}

.ingredientName {
  padding-top: 2.5vh;
  padding-bottom: 5vh;
  font-size: 3.5vh;
  font-family: Roboto Condensed;
  color: rgb(54, 55, 58);
  border-width: thin;
  margin-left: 2vw;
  margin-right: 2vw;
  margin-top: 3vh;
  border-radius: 10px;
}

.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1 1;
}

.ingredientName {
  padding-bottom: 3vh;
}

.textToQuizLink {
  margin-top: 1%;
  font-family: Roboto Condensed;
}

.textToQuizLink a {
  color: rgb(197, 145, 78);
  font-weight: bold;
}

.textToQuizLink a:hover {
  color: rgb(117, 117, 117);
}

/* responsive*/
/* for phones max-width 414px --> the biggest iphones */
@media screen and (max-width: 414px) {
  .title {
    margin-top: 5vh;
    font-size: pvw;
  }

  .slogan {
    font-size: 4vw;
  }

  .iconAndTitleWrapper {
    margin-top: 3vh;
  }

  .iconTitle {
    margin-bottom: 3px;
  }

  .row {
    flex-direction: column;
  }

  .column{
    margin-left: 10%;
  }

  h2 {
    font-size: 3.4vh;
  }

  .ingredientName {
    font-size: 3vh;
  }

  .ingredientListWrapper {
    width: 65%;
  }
}

/* for phones max-width 1024px --> for ipads*/
@media screen and (min-device-width: 415px) and (max-width: 1024px) {
  .title {
    font-size: 45px;
  }

  .ingredientListHeader {
    font-size: 26px;
  }

  h2 {
    margin-bottom: 2%;
    font-size: 2.5vh;
  }

  .slogan {
    font-size: 2.5vh;
  }

  .ingredientName {
    font-size: 3vh;
  }
}

/* ipad pro*/
@media screen and (min-device-width: 769px) and (max-width: 1024px) {
  .title {
    font-size: 5vh;
  }

  .iconTitle {
    height: 5vh;
  }

  .slogan {
    font-size: 2.6;
  }

  h2 {
    font-size: 2.5vh;
  }
}

